














import Vue from "vue";
import excel from "../libs/excel";
import Component from "vue-class-component";
import { State, Action, Getter } from "vuex-class";
import { RainState, RainStation } from "../store/rain/types";
const namespace: string = "rain";

interface Report {
  站名: string;
  站碼: string;
  行政區: string;
  過去累積10分: number;
  過去累積1小時: number;
  過去累積3小時: number;
  過去累積6小時: number;
  過去累積12小時: number;
  過去累積24小時: number;
  權責單位: string;
  資料時間: string;
}

@Component
export default class RainReportPage extends Vue {
  @State("rain") rainState: RainState;
  get reportList() {
    let ret: Report[] = [];
    for (let station of this.rainState.stations) {
      let report: Report = {
        站名: station.Name,
        站碼: station.Name,
        行政區: station.District,
        過去累積10分: station.AccRain.Acc10min,
        過去累積1小時: station.AccRain.Acc1h,
        過去累積3小時: station.AccRain.Acc3h,
        過去累積6小時: station.AccRain.Acc6h,
        過去累積12小時: station.AccRain.Acc12hr,
        過去累積24小時: station.AccRain.Acc24h,
        權責單位: station.Owner,
        資料時間: station.Timestamp
      };
      ret.push(report);
    }
    return ret;
  }

  exportLoading: boolean = false;

  exportExcel() {
    if (this.reportList.length) {
      this.exportLoading = true;
      const params = {
        title: [
          "站名",
          "站碼",
          "行政區",
          "過去累積10分",
          "過去累積1小時",
          "過去累積3小時",
          "過去累積6小時",
          "過去累積12小時",
          "過去累積24小時",
          "權責單位",
          "資料時間"
        ],
        key: [
          "站名",
          "站碼",
          "行政區",
          "過去累積10分",
          "過去累積1小時",
          "過去累積3小時",
          "過去累積6小時",
          "過去累積12小時",
          "過去累積24小時",
          "權責單位",
          "資料時間"
        ],
        data: this.reportList,
        autoWidth: true,
        filename: "雨量即時報表"
      };
      excel.export_array_to_excel(params);
      this.exportLoading = false;
    } else {
      alert("無資料!");
    }
  }
}
